@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
html{
    overflow: hidden;
    width: 100%;
    height: 100%;
    text-align: center;
    font-family: "Nunito";
    margin: 0;
    padding: 0;
}

body{
    width:100%;
    height:100%;
    margin: 0;
    padding: 0;
}

#root{
    width: 100%;
    height:100%;
    margin: 0;
    padding: 0;
}

#afterRoot{
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}


#Clock{
    display: grid;
    grid-template-columns: auto auto;
    margin-bottom: 30vh;
}

#Clock div{
    margin: 20px;
}

#break-section{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

#break-button{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: 0;
    text-align: center;
}

#session-section{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

#session-button{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: 0;
    text-align: center;
}

.btn{
    background-color: rgb(0, 0, 0);
    color: white;
    width: auto;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-left: 10px;
    padding-right: 10px;
    margin: 10px;
    border-radius: 4px;
    border-style: none;
    transition: background-color ease 800ms;
    -moz-transition: background-color ease 800ms;
    -webkit-transition: background-color ease 800ms;
    -o-transition: background-color ease 800ms;
    -ms-transition: background-color ease 800ms;
}

.btn:hover {
    background-color: rgb(238,130,238);
}

.break-time{
    width: 24px;
    padding-left: 10px;
    padding-right: 10px;
}

#countdown{
    grid-column-start: 1;
    grid-column-end: 3;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

#start_stop{
    padding: 20px 15px;
    width: 15vw;
}

#reset{
    padding: 0;
    background-color: white;
    color: black;
    text-decoration: underline;
    transition: color ease 800ms;
    -moz-transition: color ease 800ms;
    -webkit-transition: color ease 800ms;
    -o-transition: color ease 800ms;
    -ms-transition: color ease 800ms;
}

#reset:hover{
    color: rgb(238,130,238);
}
